import { Component, OnInit, ViewChild } from '@angular/core';
import { PartParoduct } from '../../_models/PartParoduct';
import { Generation } from '../../_models/Generation';
import { SpectDetail } from '../../_models/SpectDetail';
import { CPOList } from '../../_models/CPOList';
import { ModalDirective } from 'ngx-bootstrap/modal';
import { Processor } from '../../_models/Processor';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { LoadspecsService } from '../../_services/loadspecs.service';
import { AlertifyService } from '../../_services/alertify.service';
import { RequirementService } from '../../_services/requirement.service';
import { DatePipe } from '@angular/common';
import { CPLDetailsList } from '../../_models/CPLDetailsList';
import { ChatBox } from '../../_models/ChatBox';

@Component({
  selector: 'app-commit-purchase',
  templateUrl: './commit-purchase.component.html',
  styleUrls: ['./commit-purchase.component.css']
})
export class CommitPurchaseComponent implements OnInit {

  model: any = {};
  mymodel: any = {};
  updatemodel: any = {};
  chatmodel: any = {};
  chats: ChatBox[];
  roleid: any =  Number(localStorage.getItem('RoleID'));
  userid: any =  Number(localStorage.getItem('UserId'));
  makes: PartParoduct[];
  generations: Generation[];
  models: PartParoduct[];
  hdds: SpectDetail[];
  rams: SpectDetail[];
  poorders: CPOList[];
  check1: boolean;
  check2: boolean;
  check3: boolean;
  check4: boolean;
  filter1: any = {};
  filter2: any = {};
  filter3: any = {};
  cplorderdetails: CPLDetailsList[];
  processors: Processor[];
  @ViewChild('infoModal') public infoModal: ModalDirective;
  @ViewChild('EditPOModal') public EditPOModal: ModalDirective;
  @ViewChild('UpdatePOModal') public UpdatePOModal: ModalDirective;
  @ViewChild('RecievePO') public RecievePO: ModalDirective;
  @ViewChild('CPLDetails') public CPLDetails: ModalDirective;
  @ViewChild('ChatModal') public ChatModal: ModalDirective;
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: DataTables.Settings = {
      pagingType: 'full_numbers',
      pageLength: 25,
      retrieve: true,
      order: [[0, 'desc']]
  };
  dtInstance: DataTables.Api;
  isDtInitialized: boolean = false;
  dtTrigger: Subject<CPOList> = new Subject();

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService,
    private requirementservice: RequirementService, public datepipe: DatePipe) { }

  ngOnInit(): void {
    this.check1 = true;
    this.check2 = false;
    this.check3 = false;
    this.check4 = false;
    this.filter1 = 0;
    this.filter2 = 0;
    this.filter3 = 0;
    this.loadMakes();
    this.loadProcessors();
    this.loadPurchaseOrders();
  }
  loadMakes() {
    return this.loadspecsService.getMakes().subscribe((partproducts: PartParoduct[]) => {
      this.makes = partproducts.filter(proj => (proj.type === 1));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadModels(value: number) {
    return this.loadspecsService.getModels().subscribe((partproducts: PartParoduct[]) => {
      this.models = partproducts.filter(proj => (Number(proj.parentId) === Number(value)));
    }, error => {
      this.alertify.error(error);
    });
  }
  loadProcessors() {
    return this.loadspecsService.getProcessors().subscribe((processors: Processor[]) => {
      this.processors = processors;
    }, error => {
      this.alertify.error(error);
    });
  }
  loadPurchaseOrders() {
    return this.requirementservice.getCommitPurchaseOrders().subscribe((poorders: CPOList[]) => {
      this.poorders = poorders;
      console.log(this.poorders);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });

  }
  sendPurchaseOrderPost() {
    this.model['emp_id'] = localStorage.getItem('UserId');
    if (this.model.make_id === null) {
      this.alertify.error('Select Make');
      return;
    }
    if (this.model.model_id === null) {
      this.alertify.error('Select Model');
      return;
    }
    if (this.model.proc_id === null) {
      this.alertify.error('Select Processor');
      return;
    }
    if (!this.model.cpo_qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.model.cpo_price) {
      this.alertify.error('Enter Price');
      return;
    }
    if (!this.model.s_name) {
      this.alertify.error('Enter Supplier Name');
      return;
    }
    if (!this.model.cpo_eta) {
      this.alertify.error('Select Expected Delivery Date');
      return;
    }
    if (!this.model.cpo_ped) {
      this.alertify.error('Select Payment Expected Date');
      return;
    }
    if (!this.model.comment) {
      this.alertify.error('Enter Comment');
      return;
    }
    document.getElementById('postingrequest').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('postingrequest') as HTMLButtonElement).disabled = true;
    this.requirementservice.sendCommitPurchaseOrderPost(this.model).subscribe((poorders: CPOList[]) => {
      this.loadPurchaseOrders();
      this.model.make_id = null;
      this.model.model_id = null;
      this.model.proc_id = null;
      this.model.cpo_num = '';
      this.model.cpo_qty = '';
      this.model.cpo_price = '';
      this.model.s_name = '';
      this.model.comment = '';
      document.getElementById('postingrequest').innerHTML = 'Submit';
      (document.getElementById('postingrequest') as HTMLButtonElement).disabled = false;
      this.infoModal.hide();
      this.alertify.success('CPO Added Successfully');
    }, error => {
      this.alertify.error(error);
    });

  }
  CheckDetails (id: number) {
    return this.requirementservice.getCPODetails(id).subscribe((cplorderdetails: CPLDetailsList[]) => {
      this.cplorderdetails = cplorderdetails;
      console.log(this.cplorderdetails);
      this.CPLDetails.show();
    }, error => {
      this.alertify.error(error);
    });
  }
  EditPO (id: number) {
    this.mymodel = this.poorders.filter(proj => (Number(proj.id) === Number(id)))[0];
    this.mymodel.cpo_eta = this.datepipe.transform(this.mymodel.cpo_eta, 'yyyy-MM-dd');
    this.mymodel.cpo_ped = this.datepipe.transform(this.mymodel.cpo_ped, 'yyyy-MM-dd');
    this.mymodel.cpo_ped = this.datepipe.transform(this.mymodel.cpo_ped, 'yyyy-MM-dd');
    this.mymodel.comment = '';
    console.log(this.mymodel);
    this.loadModels(this.mymodel.make_id);
    this.EditPOModal.show();
  }
  UpdateCPOModal (id: number, cpo_status: number) {
    this.updatemodel.id = id;
    this.updatemodel.cpo_status = cpo_status;
    console.log(this.updatemodel);
    this.UpdatePOModal.show();
  }
  ReceiveCPOModal (id: number, cpo_status: number) {
    this.updatemodel.id = id;
    this.updatemodel.cpo_status = cpo_status;
    console.log(this.updatemodel);
    this.RecievePO.show();
  }
  UpdateCPOPost() {
    this.updatemodel['updated_by'] = localStorage.getItem('UserId');
    console.log(this.updatemodel);
    if (!this.updatemodel.comment) {
      this.alertify.error('Enter Comment');
      return;
    }
    this.requirementservice.UpdateCPOPost(this.updatemodel).subscribe((poorders: CPOList[]) => {
      this.loadPurchaseOrders();
      this.updatemodel = {};
      this.UpdatePOModal.hide();
      this.alertify.success('PO EDITED Successfully');
    }, error => {
      this.alertify.error(error);
    });
  }

  ReceiveCPOPost() {
    this.updatemodel['updated_by'] = localStorage.getItem('UserId');
    if (!this.updatemodel.receive_date) {
      this.alertify.error('Select Receiving Date');
      return;
    }
    this.requirementservice.ReceiveCPOPost(this.updatemodel).subscribe((poorders: CPOList[]) => {
      this.loadPurchaseOrders();
      this.updatemodel = {};
      this.RecievePO.hide();
      this.alertify.success('PO Received Successfully');
    }, error => {
      this.alertify.error(error);
    });
  }
  EditPurchaseOrderPost() {
    this.mymodel['updated_by'] = localStorage.getItem('UserId');
    if (this.mymodel.make_id === null) {
      this.alertify.error('Select Make');
      return;
    }
    if (this.mymodel.model_id === null) {
      this.alertify.error('Select Model');
      return;
    }
    if (this.mymodel.proc_id === null) {
      this.alertify.error('Select Processor');
      return;
    }
    if (!this.mymodel.cpo_qty) {
      this.alertify.error('Enter Qty');
      return;
    }
    if (!this.mymodel.cpo_price) {
      this.alertify.error('Enter Price');
      return;
    }
    if (!this.mymodel.s_name) {
      this.alertify.error('Enter Supplier Name');
      return;
    }
    if (!this.mymodel.cpo_eta) {
      this.alertify.error('Select Expected Delivery Date');
      return;
    }

    document.getElementById('editingrequest').innerHTML = '<i class="fa fa-circle-o-notch mt-1 fa-lg fa-spin"></i> Sending...';
    (document.getElementById('editingrequest') as HTMLButtonElement).disabled = true;
    this.requirementservice.EditCommitPurchaseOrderPost(this.mymodel).subscribe((poorders: CPOList[]) => {
      this.loadPurchaseOrders();
      this.mymodel = {};
      document.getElementById('editingrequest').innerHTML = 'Submit';
      (document.getElementById('editingrequest') as HTMLButtonElement).disabled = false;
      this.EditPOModal.hide();
      this.alertify.success('CPO EDITED Successfully');
    }, error => {
      this.alertify.error(error);
    });

  }

  activateModel(event, type: number) {
    return this.requirementservice.getCommitPurchaseOrders().subscribe((poorders: CPOList[]) => {
      console.log(type);
      if (type === 0)  {
        if (event.target.checked === true) {
          this.poorders = poorders;
          this.check1 = true; this.check2 = false; this.check3 = false; this.check4 = false;
        } else {
          this.poorders = poorders.filter(proj => (proj.cpo_status === 111));
          this.check1 = false; this.check2 = false; this.check3 = false; this.check4 = false;
       }
      }
      if (type === 1) {
        this.filter1 = type;
        this.check1 = false; this.check2 = true;
        if (event.target.checked === false) {
          this.filter1 = 0;
          this.check2 = false;
        }
        this.poorders = poorders.filter(proj => (proj.cpo_status === this.filter1 || proj.cpo_status === this.filter2
          || proj.cpo_status === this.filter3));
      }
      if (type === 3) {
        this.filter2 = type;
        this.check1 = false; this.check3 = true;
        if (event.target.checked === false) {
          this.filter2 = 0;
          this.check3 = false;
        }
        this.poorders = poorders.filter(proj => (proj.cpo_status === this.filter1 || proj.cpo_status === this.filter2
          || proj.cpo_status === this.filter3));
      }
      if (type === 4) {
        this.filter3 = type;
        this.check1 = false; this.check4 = true;
        if (event.target.checked === false) {
          this.filter3 = 0;
          this.check4 = false;
        }
        this.poorders = poorders.filter(proj => (proj.cpo_status === this.filter1 || proj.cpo_status === this.filter2
          || proj.cpo_status === this.filter3));
      }
      console.log(this.filter1);
      console.log(this.filter2);
      console.log(this.filter3);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }

  openChatModal(id: number) {
    this.model.Ref_Id = id;
    this.chatmodel.ref_id = id;
    this.chatmodel.user_id = this.userid;
    this.chatmodel.type = 0;
    return this.requirementservice.ShortfallpoviewChats(this.chatmodel).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === id));
      console.log('s');
      console.log(this.chats);
      this.loadPurchaseOrders();
      this.ChatModal.show();
  }, error => {
    this.alertify.error(error);
  });
  }

  sendMessagePost() {
    this.model.U_Id = Number(localStorage.getItem('UserId'));
    this.model.type = 0;
    if (!this.model.Text) {
      this.alertify.error('Enter Comment');
      return;
    }
    this.requirementservice.sendShortfalllPOChatPost(this.model).subscribe((chats: ChatBox[]) => {
      this.chats = chats.filter(proj => (Number(proj.status) === 0 && proj.ref_Id === this.model.Ref_Id));
      this.loadPurchaseOrders();
      this.model.Text = '';
    }, error => {
      this.alertify.error(error);
    });
  }

}
